.App {
  scroll-behavior: smooth;
  text-align: center;
  width: 100%;
  font-family: "Montserrat", sans-serif;
}

.thankyou {
  font-size: 20px;
  color: #fad7a0;
  text-align: center;
}

.zattire-button {
  background-color: #e59866;
  border: none;
  color: white;
  padding: 15px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 7%;
}

.zattire-button:hover {
  background-color: #935116;
}

.zattire-logo {
  margin-top: 30vh;
  min-width: 50%;
  max-width: 300px;
  transition-duration: 2s;
}

.logos {
  margin-top: 10vh;
  width: 400px;
  max-width: 400px;
}

@media only screen and (max-width: 600px) {
  .logos {
    width: 200px;
  }
}

.App-header {
  background-color: #e56b80;
  min-height: 100vh;
  color: white;
}
